import { useEffect, useState } from 'react';

const seconds = 60000;
const minutes = 600000;

const SaveStatus = ({
  className,
  state,
}: {
  className: string;
  state: 'error' | 'idle' | 'loading' | 'success';
}) => {
  const [lastSave, setLastSave] = useState<Date>(null);
  const [now, setNow] = useState<Date>(new Date());

  useEffect(() => {
    if (state === 'success') {
      setLastSave(new Date());
    }
  }, [state]);

  useEffect(() => {
    const timer = setTimeout(() => {
      setNow(new Date());
    }, 30000);

    return () => {
      clearTimeout(timer);
    };
  }, [now]);

  if (!lastSave) {
    return null;
  }

  const diff = now.getTime() - lastSave.getTime();
  const text = lastSave
    ? diff <= seconds
      ? 'a few seconds ago'
      : diff <= minutes
      ? 'a few minutes ago'
      : lastSave.toLocaleString()
    : '';

  return text ? <p className={`${className} text-sm`}>Last saved {text}</p> : null;
};

export default SaveStatus;
