import { useParams } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useApp from '../../../hooks/app/useApp';
import ActionButton from '../../Buttons/ActionButton';
import Grid from '../../Icons/Grid/Grid';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import DrivePermissionView from '../../PermissionViews/DrivePermissionView/DrivePermissionView';
import PermissionView from '../../PermissionViews/PermissionView/PermissionView';
import Section from '../../Sections/Section';

const AppDetails = () => {
  const { appKey } = useParams();
  const decodedAppKey = decodeURIComponent(appKey);
  const {
    fetch: { data: app, isLoading: appLoading },
    revokeApp: { mutate: revokeApp, status: revokeAppStatus },
    allowApp: { mutate: allowApp, status: allowAppStatus },
  } = useApp({ appId: decodedAppKey });

  if (appLoading) {
    <>Loading</>;
  }

  if (!app) {
    return <>No matching app found</>;
  }

  return (
    <>
      <PageMeta
        icon={Grid}
        title={`${app.name}`}
        breadCrumbs={[{ href: '/owner/apps', title: 'My apps' }, { title: app.name ?? '' }]}
        actions={
          <>
            {app.isRevoked ? (
              <ActionButton
                type="primary"
                className="my-auto ml-2"
                onClick={() => allowApp({ appId: decodedAppKey })}
                state={allowAppStatus}
                confirmOptions={{
                  title: t('Allow App'),
                  buttonText: t('Allow'),
                  body: `${t('Are you sure you want to restore')} ${app.name} ${t(
                    'and allow access to your identity'
                  )}`,
                }}
              >
                Allow app
              </ActionButton>
            ) : (
              <ActionButton
                type="remove"
                className="my-auto ml-2"
                onClick={() => revokeApp({ appId: decodedAppKey })}
                state={revokeAppStatus}
                confirmOptions={{
                  title: t('Revoke App'),
                  buttonText: t('Revoke'),
                  body: `${t('Are you sure you want to revoke')} ${app.name} ${t(
                    'from all access to your identity'
                  )}`,
                }}
              >
                Revoke app
              </ActionButton>
            )}
          </>
        }
      />

      {app.grant.permissionSet?.keys ? (
        <Section title={t('Access')} isOpaqueBg={true}>
          <div className="-my-4">
            {app.grant.permissionSet.keys.map((permissionLevel) => {
              return (
                <PermissionView
                  key={`${permissionLevel}`}
                  permission={permissionLevel}
                  className="my-4"
                />
              );
            })}
          </div>
        </Section>
      ) : null}
      <Section title={t('Access on the following drives')} isOpaqueBg={true}>
        {app.grant?.driveGrants?.length ? (
          <div className="-my-4">
            {app.grant?.driveGrants?.map((grant) => {
              return (
                <DrivePermissionView
                  key={`${grant?.drive?.alias}-${grant?.drive?.type}`}
                  driveGrant={grant}
                  className="my-4"
                />
              );
            })}
          </div>
        ) : (
          <div className="flex flex-row">
            <p className="my-auto">{t("This app doesn't have any access")}</p>
          </div>
        )}
      </Section>
    </>
  );
};

export default AppDetails;
